export const required = value => value ? undefined : '–≠—Ç–æ –ø–æ–ª–µ –æ–±—è–∑–∞—Ç–µ–ª—å–Ω–æ –¥–ª—è –∑–∞–ø–æ–ª–Ω–µ–Ω–∏—è';

export const maxLength = (maxLength) => (value) => (value && value.length <= maxLength) ? undefined : `max length is ${maxLength} symbols`;

export const minmaxLength = (min, max) => (value) => {
    return (value && value.length >= min && value.length <= max) ? undefined : `–î–æ–ª–∂–Ω–æ –±—ã—Ç—å –æ—Ç ${min} –¥–æ ${max} —Å–∏–º–≤–æ–ª–æ–≤`;
};


export const phoneVerification = (value) => {
    let regexp = /^(\+7|8)\s?(9\d{2}|\(9\d{2}\))(\s|-)*(\d{7}|\d{3}-\d{2}-\d{2})$/;
    return (value.match(regexp) ? undefined : `—Ç–µ–ª–µ—Ñ–æ–Ω –≤ —Ñ–æ—Ä–º–∞—Ç–µ: +7 (900) 000-00-00`);
};


export const emailVerification = (value) => {
    let regexp = /^\w+((-|.)\w+)*@\w+(-\w+)*\.\w{2,}$/;
    return (value.match(regexp) ? undefined : `e-mail –≤ —Ñ–æ—Ä–º–∞—Ç–µ: students-yandex@yandex.ru`);
};

