import React from "react";
import styles from "./Menu.module.css";
import logoGreen from "./../../assets/logoGreen.svg";
import closeMenu from "./../../assets/closeMenu.svg";
import {NavLink} from "react-router-dom";
import {connect} from "react-redux";
import {toggleMenu} from "../../redux/commonReducer";
import {compose} from "redux";

const Menu = (props) => {
    const layer = React.createRef();

    // useEffect(() => {
    //     document.addEventListener('keydown', closeMenuEsc)
    // }, []);

    document.addEventListener('keydown', closeMenuEsc);

    function closeMenuEsc(e) {
        if (e.keyCode === 27) {
            props.toggleMenu(false);
        }
    }

    function clickOutside(e) {
        if (e.target === layer.current) {
            props.toggleMenu(false);
        }
    }

    return (
        <div className={`${styles.wrapper} ${props.isOpenMenu ? styles.openMenu : null}`} onClick={clickOutside}>
            <div className={styles.layer} ref={layer}></div>
            <div className={styles.container}>
                <img src={logoGreen} alt="–ª–æ–≥–æ—Ç–∏–ø" className={styles.logoGreen}/>
                <img src={closeMenu} alt="–∑–∞–∫—Ä—ã—Ç—å" className={styles.closebtn} onClick={() => {
                    props.toggleMenu(false)
                }}/>
                <menu className={styles.sidenav} onClick={() => {props.toggleMenu(false)}}>
                    <NavLink to="/drycleaning/furniture" className={styles.link}>–•–∏–º—á–∏—Å—Ç–∫–∞ –º–µ–±–µ–ª–∏</NavLink>
                    <NavLink to="/drycleaning/matress" className={styles.link}>–•–∏–º—á–∏—Å—Ç–∫–∞ –º–∞—Ç—Ä–∞—Å–æ–≤</NavLink>
                    <NavLink to="/drycleaning/carpet" className={styles.link}>–•–∏–º—á–∏—Å—Ç–∫–∞ –∫–æ–≤—Ä–æ–≤</NavLink>
                    <NavLink to="#" className={styles.link}>–ü–æ–¥–¥–µ—Ä–∂–∏–≤–∞—é—â–∞—è —É–±–æ—Ä–∫–∞</NavLink>
                    <NavLink to="#" className={styles.link}>–ì–µ–Ω–µ—Ä–∞–ª—å–Ω–∞—è —É–±–æ—Ä–∫–∞</NavLink>
                    <NavLink to="#" className={styles.link}>–£–±–æ—Ä–∫–∞ –ø–æ—Å–ª–µ —Ä–µ–º–æ–Ω—Ç–∞</NavLink>
                    <NavLink to="#" className={styles.link}>–ú—ã—Ç—å–µ –æ–∫–æ–Ω / –±–∞–ª–∫–æ–Ω–æ–≤</NavLink>
                    <NavLink to="#" className={styles.link}>–ú—ã—Ç—å–µ —Ñ–∞—Å–∞–¥–æ–≤</NavLink>
                    <NavLink to="#" className={styles.link}>–ù–∞—à–∏ —Ü–µ–Ω—ã</NavLink>
                    <NavLink to="#" className={`${styles.link} ${styles.sales}`}>–ê–∫—Ü–∏–∏</NavLink>
                    <NavLink to="#" className={styles.link}>–ö–æ–Ω—Ç–∞–∫—Ç—ã</NavLink>
                </menu>
            </div>
        </div>
    );
};


function mapStateToProps(state) {
    return {
        isOpenMenu: state.common.isOpenMenu,
    }
}

let mapDispatchToProps = {
    toggleMenu,
};


export default compose(
    connect(mapStateToProps, mapDispatchToProps),
)(Menu);
