import React from "react";
import CoolButton from "../../common/buttons/CoolButton";
import styles from "./PopupAskContent.module.css";
import {renderInput, renderTextarea} from "../../common/ValidatorForms/ValidatorForms";
import {
    emailVerification,
    maxLength,
    minmaxLength,
    phoneVerification,
    required
} from "../../common/ValidatorForms/validators";
import {Field, reduxForm} from "redux-form";
import {sendAPI} from "../../api/api";



const maxLength700 = maxLength(700);
const minmaxLength2_30 = minmaxLength(2, 30);

let AskForm = (props) => {
    return (
        <form onSubmit={props.handleSubmit} className={styles.form}>
            <div className={styles.inputPlace}>
                <Field component={renderInput} type="text" name="name" placeholder={'–í–∞—à–µ –∏–º—è*'} validate={[required, minmaxLength2_30]}/>
            </div>
            <div className={styles.inputPlace}>
                <Field component={renderInput} type="tel" name="phone" placeholder={'–í–∞—à –Ω–æ–º–µ—Ä —Ç–µ–ª–µ—Ñ–æ–Ω–∞*'} validate={[required, phoneVerification]}/>
            </div>
            <div className={styles.inputPlace}>
                <Field component={renderInput} type="text" name="email" placeholder={'–í–∞—à —ç–ª–µ–∫—Ç—Ä–æ–Ω–Ω—ã–π –∞–¥—Ä–µ—Å*'} validate={[required, emailVerification]}/>
            </div>
            <div className={styles.inputPlace}>
                <Field component={renderTextarea} type="textarea" name="question" placeholder={'–í–∞—à –≤–æ–ø—Ä–æ—Å'} validate={[required, maxLength700]}/>
            </div>
            <div  className={styles.button}>
                <CoolButton onClick={props.close} isDisabled={props.invalid} size={'middle'} text={'–ó–∞–¥–∞—Ç—å –≤–æ–ø—Ä–æ—Å'}/>
            </div>
        </form>
    );
};

AskForm = reduxForm({form: 'askForm'})(AskForm);


const PopupAskContent = (props) => {

    const onSubmit  = (formData) => {
        const {name, phone, email, question} = formData;
        sendAPI.sendAskForm({
            name,
            phone,
            email,
            question
        });
    };


  return (
      <div className={styles.common}>
          <h1 className={styles.title}>–ó–∞–¥–∞–π—Ç–µ –í–∞—à –≤–æ–ø—Ä–æ—Å</h1>
          <p className={styles.description}>–ó–∞–¥–∞–π—Ç–µ –í–∞—à –≤–æ–ø—Ä–æ—Å –∏ –º—ã –æ—Ç–≤–µ—Ç–∏–º –Ω–∞ –Ω–µ–≥–æ –≤ –±–ª–∏–∂–∞–π—à–µ–µ –≤—Ä–µ–º—è.</p>
          <AskForm onSubmit={(formData) => {onSubmit(formData); props.update()}}/>
      </div>
  );
};

export default PopupAskContent;