import React from "react";
import CoolButton from "../../common/buttons/CoolButton";
import styles from "./PopupFeedbackContent.module.css";
import {renderInput, renderTextarea} from "../../common/ValidatorForms/ValidatorForms";
import {
    emailVerification,
    maxLength,
    minmaxLength,
    phoneVerification,
    required
} from "../../common/ValidatorForms/validators";
import {Field, reduxForm} from "redux-form";
import {sendAPI} from "../../api/api";


const maxLength700 = maxLength(700);
const minmaxLength2_30 = minmaxLength(2, 30);

let FeedbackForm = (props) => {
    return (
        <form onSubmit={props.handleSubmit} className={styles.form}>
            <div className={styles.inputPlace}>
                <Field component={renderInput} type="text" name="name" placeholder={'–í–∞—à–µ –∏–º—è*'} validate={[required, minmaxLength2_30]}/>
            </div>
            <div className={styles.inputPlace}>
                <Field component={renderInput} type="tel" name="phone" placeholder={'–í–∞—à –Ω–æ–º–µ—Ä —Ç–µ–ª–µ—Ñ–æ–Ω–∞*'} validate={[required, phoneVerification]}/>
            </div>
            <div className={styles.inputPlace}>
                <Field component={renderInput} type="text" name="email" placeholder={'–í–∞—à —ç–ª–µ–∫—Ç—Ä–æ–Ω–Ω—ã–π –∞–¥—Ä–µ—Å*'} validate={[required, emailVerification]}/>
            </div>
            <div className={styles.inputPlace}>
                <Field component={renderTextarea} type="textarea" name="comment" placeholder={'–í–∞—à –æ—Ç–∑—ã–≤'} validate={[required, maxLength700]}/>
            </div>
            <div  className={styles.button}>
                <CoolButton onClick={props.close} isDisabled={props.invalid} size={'middle'} text={'–û—Å—Ç–∞–≤–∏—Ç—å –æ—Ç–∑—ã–≤'}/>
            </div>
        </form>
    );
};

FeedbackForm = reduxForm({form: 'askForm'})(FeedbackForm);


const PopupFeedbackContent = (props) => {

    const onSubmit  = (formData) => {
        let {name, phone, email, comment} = formData;
        sendAPI.sendFeedback({
            name,
            phone,
            email,
            comment
        });
    };

  return (
      <div className={styles.common}>
          <h1 className={styles.title}>–û—Å—Ç–∞–≤—å—Ç–µ –í–∞—à –æ—Ç–∑—ã–≤</h1>
          <p className={styles.description}>–ü–æ–¥–µ–ª–∏—Ç–µ—Å—å –í–∞—à–∏–º–∏ –≤–ø–µ—á–∞—Ç–ª–µ–Ω–∏—è–º–∏ –æ–± –æ–∫–∞–∑–∞–Ω–Ω–æ–π –í–∞–º —É—Å–ª—É–≥–µ.
              –ù–∞–º –≤–∞–∂–Ω—ã –í–∞—à–∏ –∫–æ–º–º–µ–Ω—Ç–∞—Ä–∏–∏, —Ç–∞–∫ –∫–∞–∫ –º—ã –ø–æ—Å—Ç–æ—è–Ω–Ω–æ —Ä–∞–±–æ—Ç–∞–µ–º –Ω–∞–¥ –ø–æ–≤—ã—à–µ–Ω–∏–µ–º —É—Ä–æ–≤–Ω—è —Å–µ—Ä–≤–∏—Å–∞</p>
          <FeedbackForm onSubmit={(formData) => {onSubmit(formData); props.update()}}/>
      </div>
  );
};

export default PopupFeedbackContent;