import React from "react";
import CoolButton from "../../common/buttons/CoolButton";
import styles from "./PopupOrderContent.module.css";
import {renderInput} from "../../common/ValidatorForms/ValidatorForms";
import {minmaxLength, phoneVerification, required} from "../../common/ValidatorForms/validators";
import {Field, reduxForm} from "redux-form";
import {sendAPI} from "../../api/api";



const minmaxLength230 = minmaxLength(2, 30);

let OrderForm = (props) => {
    return (
        <form onSubmit={props.handleSubmit} className={styles.form}>
            <div className={styles.inputPlace}>
                <Field component={renderInput} type="text" name="name" placeholder={'–í–∞—à–µ –∏–º—è*'} validate={[required, minmaxLength230]}/>
            </div>
            <div className={styles.inputPlace}>
                <Field component={renderInput} type="tel" name="phone" placeholder={'–í–∞—à –Ω–æ–º–µ—Ä —Ç–µ–ª–µ—Ñ–æ–Ω–∞*'} validate={[required, phoneVerification]}/>
            </div>
            <div  className={styles.button}>
                <CoolButton isDisabled={props.invalid} size={'middle'} text={'–û—Å—Ç–∞–≤–∏—Ç—å –∑–∞—è–≤–∫—É'}/>
            </div>
        </form>
    );
};

OrderForm = reduxForm({form: 'orderForm'})(OrderForm);




let PopupOrderContent = (props) => {
    const onSubmit  = (formData) => {
        let {name, phone} = formData;
        sendAPI.sendOrderFormFull({
            name,
            phone,
            typeOfWork: props.addInfo.typeOfWork,
            meters: props.addInfo.meters,
            addServices: props.addInfo.addServices,
            commonPrice: props.addInfo.commonPrice
        });
    };

    // async function onSubmit(formData) {
    //     const {name, phone} = formData;
    //
    //     const form = await axios.post('/sendOrderFormFull', {
    //         name,
    //         phone,
    //         typeOfWork: props.addInfo.typeOfWork,
    //         meters: props.addInfo.meters,
    //         addServices: props.addInfo.addServices,
    //         commonPrice: props.addInfo.commonPrice
    //     });
    // }

  return (
      <div className={styles.common}>
          <h1 className={styles.title}>–û–±—Ä–∞—Ç–Ω—ã–π –∑–≤–æ–Ω–æ–∫</h1>
          <p className={styles.description}>–û—Å—Ç–∞–≤—å—Ç–µ –∑–∞—è–≤–∫—É –∏ –Ω–∞—à —Å–æ—Ç—Ä—É–¥–Ω–∏–∫ —Å–≤—è–∂–µ—Ç—Å—è —Å –í–∞–º–∏ –≤ —Å–∞–º–æ–µ –±–ª–∏–∂–∞–π—à–µ–µ –≤—Ä–µ–º—è.</p>
          <OrderForm onSubmit={(formData) => {onSubmit(formData); props.update()}}/>
      </div>
  );
};



export default PopupOrderContent;